import Image from "next/image";
import Link from "next/link";

export default function GamePage() {
  return (
    <div className="relative min-h-screen overflow-hidden">
      {/* 배경 이미지 */}
      <div className="absolute inset-0 z-0">
        <Image
          src="/images/backgrounds/background-main.png"
          alt="헬스 히어로 메인 배경"
          fill
          className="object-cover"
          priority
        />
      </div>

      {/* 고정 헤더 */}
      <div className="fixed top-6 left-0 right-0 z-30 bg-transparent">
        <div className="flex items-center justify-between px-6 py-3 h-[60px]">
          {/* 캐릭터 & 레벨 */}
          <div className="flex items-center gap-5">
            <div className="relative">
              {/* 캐릭터 원형 배경 */}
              <Image
                src="/images/ui/block02.png"
                alt="캐릭터 배경"
                width={40}
                height={40}
                className="relative z-10"
              />
              {/* 캐릭터 이미지 */}
              <Image
                src="/images/characters/level-20.png"
                alt="캐릭터"
                width={30}
                height={30}
                className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 z-20"
              />
              {/* 레벨 블록 */}
              <Image
                src="/images/ui/block01.png"
                alt="레벨 배경"
                width={70}
                height={24}
                className="absolute top-1/2 left-6 transform -translate-y-1/2 z-0"
                style={{ width: '70px', height: '24px', minWidth: '70px', minHeight: '24px' }}
              />
              {/* 레벨 텍스트 */}
              <span className="absolute top-1/2 left-6 transform -translate-y-1/2 z-10 text-sm font-bold text-[#8B4513] whitespace-nowrap flex items-center justify-center w-[70px] ml-1">
                Lv.20
              </span>
            </div>
          </div>

          {/* 하트 */}
          <div className="flex items-center gap-5">
            <div className="relative">
              {/* 하트 아이콘 */}
              <Image
                src="/images/items/icon-heart.png"
                alt="하트"
                width={44}
                height={44}
                className="relative z-10"
              />
              {/* 하트 내부 숫자 */}
              <span className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 z-20 text-white text-base font-normal">
                5
              </span>
              {/* 광고 버튼 */}
              <Image
                src="/images/items/button-ad.png"
                alt="광고 버튼"
                width={16}
                height={16}
                className="absolute bottom-0 right-0 z-20"
              />
              {/* 타이머 블록 */}
              <Image
                src="/images/ui/block01.png"
                alt="타이머 배경"
                width={70}
                height={24}
                className="absolute top-1/2 left-6 transform -translate-y-1/2 z-0"
                style={{ width: '70px', height: '24px', minWidth: '70px', minHeight: '24px' }}
              />
              {/* 타이머 텍스트 */}
              <span className="absolute top-1/2 left-6 transform -translate-y-1/2 z-10 text-sm font-bold text-[#8B4513] whitespace-nowrap flex items-center justify-center w-[70px] ml-1">
                4분12초
              </span>
            </div>
          </div>

          {/* 점수 */}
          <div className="flex items-center gap-5">
            <div className="relative">
              {/* 별 아이콘 */}
              <Image
                src="/images/items/Icon-star.png"
                alt="별"
                width={40}
                height={40}
                className="relative z-10"
              />
              {/* 점수 블록 */}
              <Image
                src="/images/ui/block01.png"
                alt="점수 배경"
                width={70}
                height={24}
                className="absolute top-1/2 left-6 transform -translate-y-1/2 z-0"
                style={{ width: '70px', height: '24px', minWidth: '70px', minHeight: '24px' }}
              />
              {/* 점수 텍스트 */}
              <span className="absolute top-1/2 left-6 transform -translate-y-1/2 z-10 text-sm font-bold text-[#8B4513] whitespace-nowrap flex items-center justify-center w-[70px] ml-1">
                10,200
              </span>
            </div>
          </div>

          {/* 설정 버튼 */}
          <div className="flex items-center">
            <Image
              src="/images/items/button-setting.png"
              alt="설정"
              width={24}
              height={24}
              className="cursor-pointer hover:opacity-80 transition-opacity"
            />
          </div>
        </div>
      </div>

      {/* 메인 콘텐츠 */}
      <div className="relative z-10 flex flex-col items-center justify-center min-h-screen px-4 pt-[72px]">

        {/* 중앙 맵 영역 */}
        <div className="flex-1 flex items-center justify-center">
          <div className="text-center">
            <h1 className="text-white text-3xl font-bold mb-8 drop-shadow-lg">
              게임 맵 (개발 예정)
            </h1>
            <p className="text-white text-lg mb-8 drop-shadow-lg">
              페이즈와 스테이지가 여기에 표시됩니다
            </p>
          </div>
        </div>

        {/* 하단 네비게이션 */}
        <div className="absolute bottom-0 left-0 right-0 p-4">
          <div className="flex justify-center gap-4">
            <Link href="/">
              <button className="bg-gray-600 hover:bg-gray-700 text-white font-bold py-2 px-4 rounded-lg transition-all duration-300">
                홈으로
              </button>
            </Link>
            <button className="bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-lg transition-all duration-300">
              첫 번째 스테이지 시작
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
